import {React, PropTypes, getLabel, Material} from "../imports";

export class PartsView extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const data = this.props.getValue();
    return (<div className="parts">
      <div className="col-sm-4">
        <h4 className="parts-title">{getLabel("LABEL_CROWN")}</h4>
        <ul className="list-group">
          {data && data.filter(part => part[Material.MATERIALTYPE] === "crown").map(part =>
            (<li key={part[Material._ID]}
                 className="list-group-item"> {part[Material._ID]} - {part[Material.TEXTURE]}
            </li>))}
        </ul>
      </div>
      <div className="col-sm-4">
        <h4 className="parts-title">{getLabel("LABEL_CRYSTAL")}</h4>
        <ul className="list-group">
          {data && data.filter(part => part[Material.MATERIALTYPE] === "crystal").map(part =>
            (<li key={part[Material._ID]}
                 className="list-group-item"> {part[Material._ID]} - {part[Material.TEXTURE]}
            </li>))}
        </ul>
      </div>
      <div className="col-sm-4">
        <h4 className="parts-title">{getLabel("LABEL_STRAP")}</h4>
        <ul className="list-group">
          {data && data.filter(part => part[Material.MATERIALTYPE] === "strap").map(part =>
            (<li key={part[Material._ID]}
                 className="list-group-item "> {part[Material._ID]} - {part[Material.TEXTURE]}
            </li>))}
        </ul>
      </div>
    </div>);
  }

}

PartsView.propTypes = {
  getValue: PropTypes.func.isRequired,
};



