import {Item, getLabel} from "../imports";
import {moment} from "../../ShipmentEntry/imports";

export default [{
  label: getLabel("LABEL_SERIAL_NO"),
  field: Item.SERIALNO,
  primaryKey: true,
  skipRender: false
},
  {
    label: getLabel("LABEL_NAME"),
    field: Item.NAME,
    primaryKey: false,
    skipRender: false
  },
  {
    label: getLabel("LABEL_TESTED_BY"),
    field: Item.TESTEDBY,
    primaryKey: false,
    skipRender: false,
  },
  {
    label: getLabel("LABEL_DATE_TESTED"),
    field: Item.DATETESTED,
    primaryKey: false,
    skipRender: false,
    transform: (value) => moment(value).format("YYYY-MM-DD")
  },
  {
    label: getLabel("LABEL_DATE_CREATED"),
    field: Item.DATECREATED,
    primaryKey: false,
    skipRender: false,
    transform: (value) => moment(value).format("YYYY-MM-DD")
  }];



