import { MaterialPage } from "../components/Process/Material";
export default { 
	root: "Process",
	name: "material",
	pages: [
		{ icon: "inbox", path: "/material", component: MaterialPage, name: "material", label: "LABEL_MATERIAL" },
		{ path: "/material/new", component: MaterialPage, skipLink: true },
		{ path: "/material/view/:id", component: MaterialPage, skipLink: true }]
};



