import {FluidForm, Item, Material, FluidFunc} from "../imports";
import {PAGE_NAME} from "../constants";
import {ITEM_REMOVE_PART} from "../fluid.info";

export default ({props, params}) => {
  const currentParts = FluidForm.getValue(props.pageForm, Item.PARTS);
  const newParts = [...currentParts];
  newParts.forEach((material, index) => {
    if (material[Material._ID] === params.materialId()) {
      newParts.splice(index, 1);
      FluidFunc.start(ITEM_REMOVE_PART, {
        materialId: params.materialId()
      });
    }
  });
  FluidForm.set(PAGE_NAME, Item.PARTS, newParts);
};



