import {FLUID_TRIGGER_COMMAND} from './constants';
import {FluidFunc} from "./imports";

export const triggerCommands = (pageName) => {
  return `${pageName}_${FLUID_TRIGGER_COMMAND}`;
};


export const executeCommand = (pageName, command, options) => {
  return FluidFunc.start(triggerCommands(pageName), {command, ...options});
};



