import addPart from "./addPart";
import removePart from "./removePart";
import {COMMAND_ADD_PART, COMMAND_REMOVE_PART} from "../constants";

export default (command, instance) => {
  switch (command) {
    case COMMAND_ADD_PART:
      addPart(instance);
      break;
    case COMMAND_REMOVE_PART:
      removePart(instance);
      break;
  }
};



