import {Material, getLabel} from "../imports";

export default () => [{
  label: undefined,
  field: Material._ID,
  primaryKey: true,
  skipRender: true
},
  {
    label: getLabel("LABEL_TEXTURE"),
    field: Material.TEXTURE,
    primaryKey: false,
    skipRender: false
  },
  {
    label: undefined,
    field: Material.COUNTRY,
    primaryKey: false,
    skipRender: true
  },
  {
    label: getLabel("LABEL_MATERIAL_TYPE"),
    field: Material.MATERIALTYPE,
    primaryKey: false,
    skipRender: false
  },
  {
    label: getLabel("LABEL_AVAILABLE"),
    field: Material.AVAILABLE,
    primaryKey: false,
    skipRender: false
  },
  {
    label: getLabel("LABEL_DATE_MANUFACTURED"),
    field: Material.DATEMANUFACTURED,
    primaryKey: false,
    skipRender: false
  },
  {
    label: getLabel("LABEL_DATE_CREATED"),
    field: Material.DATECREATED,
    primaryKey: false,
    skipRender: false
  }];



