import {Material, getLabel} from "../imports";
import {moment} from "../../ShipmentEntry/imports";

export default [{
  label: getLabel("undefined"),
  field: Material._ID,
  primaryKey: true,
  skipRender: true
},
  {
    label: getLabel("LABEL_TEXTURE"),
    field: Material.TEXTURE,
    primaryKey: false,
    skipRender: false
  },
  {
    label: getLabel("LABEL_MATERIAL_TYPE"),
    field: Material.MATERIALTYPE,
    primaryKey: false,
    skipRender: false
  },
  {
    label: getLabel("LABEL_AVAILABLE"),
    field: Material.AVAILABLE,
    primaryKey: false,
    skipRender: false,
    transform: (value) => value ? "yes" : "no"
  },
  {
    label: getLabel("LABEL_DATE_CREATED"),
    field: Material.DATECREATED,
    primaryKey: false,
    skipRender: false,
    transform: (value) => moment(value).format("YYYY-MM-DD")
  }];



