import {CrudPage, getLabel, Material, moment} from "./imports";
import {FormSpecs, TableColumns} from "./api/";
import {PAGE_NAME} from "./constants";

export const MaterialPage = CrudPage({
  pageName: PAGE_NAME,
  FormSpecs,
  TableColumns,
  page: {
    banner: "./home-banner.jpg",
    label: getLabel("LABEL_MATERIAL"),
    icon: "inbox"
  },
  formProps: {
    fieldClass: field => {
      switch (field) {
        case Material.MATERIALTYPE:
          return "col-sm-4 col-sm-offset-right-4";
        case Material.AVAILABLE:
          return "col-sm-4 col-sm-offset-right-8";
        default:
          return "col-sm-4";
      }
    },
    viewValueTransformer: field => {
      switch (field) {
        case Material.AVAILABLE:
          return (value) => value ? "yes" : "no";
        case Material.DATECREATED:
        case Material.DATEMANUFACTURED:
          return (value) => {
            if (value) {
              return moment(value).format("YYYY-MM-DD");
            } else {
              return "N/A";
            }
          };

      }
    }
  },
  overrideHeaders: (page, {defaultControls}) => {
    switch (page) {
      case "list":
      case "view":
        return {
          back: defaultControls.back,
          refresh: defaultControls.refresh
        };
      default:
        return {};
    }
  }
});



