import { ItemPage } from "../components/Process/Item";
export default { 
	root: "Process",
	name: "item",
	pages: [
		{ icon: "puzzle-piece", path: "/item", component: ItemPage, name: "item", label: "LABEL_ITEM" },
		{ path: "/item/new", component: ItemPage, skipLink: true },
		{ path: "/item/view/:id", component: ItemPage, skipLink: true }]
};


