import {FluidFunc} from "../imports";
import {PAGE_NAME} from "../constants";

const NODE_API = process.env.NODE_API;

FluidFunc.create(PAGE_NAME)
  .onStart(({action, id}) => {
    switch (action()) {
      case "getListData":
        return new Promise((resolve, reject) => {
          fetch(`${NODE_API}/api/org.lifecycle.watch.RawMaterial`)
            .then(result => result.json())
            .then(json => {
              const data = {};
              data[PAGE_NAME] = json;
              resolve({data});
            })
            .catch(error => {
              reject(error);
            });
        });

      case "getDataById":
        return new Promise((resolve, reject) => {
          fetch(`${NODE_API}/api/org.lifecycle.watch.RawMaterial/${id()}`).then(result => result.json())
            .then(json => {
              resolve({data: json});
            })
            .catch(error => {
              reject(error);
            });
        });
    }
  });



