import {FluidApi, Material} from "../../imports";

const MATERIAL = "material";

export default {
  development: ({materialType}) => new Promise((resolve) => {
    FluidApi.storage(MATERIAL).then(({data}) => {
      let materials = data().filter(material => material[Material.MATERIALTYPE] === materialType());
      resolve({
        data: materials
      });
    });
  }),
  production: () => new Promise(() => {

  })
};



