import {Item, getLabel} from "../imports";

export default () => [{
  label: getLabel("LABEL_SERIAL_NO"),
  field: Item.SERIALNO,
  primaryKey: true,
  skipRender: false,
  data: {
    require: true
  }
},
  {
    label: getLabel("LABEL_NAME"),
    field: Item.NAME,
    primaryKey: false,
    skipRender: false,
    data: {
      require: true
    }
  },
  {
    label: getLabel("LABEL_DATE_MANUFACTURED"),
    field: Item.DATEMANUFACTURED,
    primaryKey: false,
    skipRender: false,
    data: {
      require: true
    }
  },
  {
    label: getLabel("LABEL_COMPANY"),
    field: Item.COMPANY,
    primaryKey: false,
    skipRender: true
  },
  {
    label: getLabel("LABEL_COUNTRY"),
    field: Item.COUNTRY,
    primaryKey: false,
    skipRender: true
  },
  {
    label: getLabel("LABEL_TESTED_BY"),
    field: Item.TESTEDBY,
    primaryKey: false,
    skipRender: false,
    public: true,
    data: {
      require: true
    }
  },
  {
    label: getLabel("LABEL_DATE_TESTED"),
    field: Item.DATETESTED,
    primaryKey: false,
    skipRender: false,
    public: true
  },
  {
    label: getLabel("LABEL_PARTS"),
    field: Item.PARTS,
    primaryKey: false,
    skipRender: false,
    public: true,
    data: {
      require: true
    }
  }];



