import {React, getLabel, FluidApi, Material, executeCommand, FluidFunc} from "../imports";
import {PAGE_NAME, COMMAND_ADD_PART} from "../constants";
import {ITEM_REMOVE_PART} from "../fluid.info";

export class SearchMaterials extends React.Component {
  constructor(props) {
    super(props);
    this.onSubmit = this._onSubmit.bind(this);
    this.onSelectType = this._onSelectType.bind(this);
    this.onInputSearch = this._onInputSearch.bind(this);
    this.addPart = this._addPart.bind(this);
    this.filterSelectedParts = this._filterSelectedParts.bind(this);
    this.removePart = this._removePart.bind(this);
    this.state = {
      type: "strap",
      filterText: "",
      parts: []
    };
    FluidFunc.create(ITEM_REMOVE_PART)
      .onStart(this.removePart)
      .spec('materialId', {require: true});
  }

  componentWillMount() {
    this.refresh();
  }

  refresh(type) {
    this.loadingOn();
    FluidApi.execute("getMaterialsByType", {
      materialType: type || this.state.type
    }).then(({getMaterialsByType}) => {
      this.setData(getMaterialsByType("data")());
      this.loadingOff();
    }).catch(error => {
      this.error(error);
      this.loadingOff();
      this.setData([]);
    });
  }

  error(error) {
    this.setState({error});
  }

  setData(data) {
    this.setState({data});
  }

  loadingOn() {
    this.setState({loading: true});
  }

  loadingOff() {
    this.setState({loading: false});
  }

  _addPart(material) {
    executeCommand(PAGE_NAME, COMMAND_ADD_PART, {material});
    this._addPartToState(material);
  }

  _onSubmit(evt) {
    evt.preventDefault();
  }

  _onInputSearch(evt) {
    this.setState({filterText: evt.target.value});
  }

  _onSelectType(evt) {
    this.setState({
      type: evt.target.value
    });
    this.refresh(evt.target.value);
  }

  _addPartToState(material) {
    const parts = this.state.parts;
    const newParts = [...parts];
    newParts.push(material[Material._ID]);
    this.setState({
      parts: newParts
    });
  }

  _filterSelectedParts(part) {
    return !(this.state.parts.indexOf(part[Material._ID]) > -1);
  }

  _removePart({materialId}) {
    const parts = this.state.parts;
    const newParts = [...parts];
    newParts.forEach((part, index) => {
      if (part === materialId()) {
        newParts.splice(index, 1);
      }
    });
    this.setState({
      parts: newParts
    });
  }

  render() {
    return (<div className="search-materials">
      <div>
        <form name="search" onSubmit={this.onSubmit}>
          <div className="form-group link-search clearfix adapt-link-theme">
            <input onChange={this.onInputSearch} name="searchValue" className="form-control "
                   placeholder={getLabel("LABEL_SEARCH_MATERIALS")}/>
          </div>
          <div className="form-group">
            <select value={this.state.type} onChange={this.onSelectType} name="materialType" className="form-control">
              <option value="">{getLabel("LABEL_SELECT_OPTIONS")}</option>
              <option value="crown">{getLabel("LABEL_CROWN")}</option>
              <option value="crystal">{getLabel("LABEL_CRYSTAL")}</option>
              <option value="strap">{getLabel("LABEL_STRAP")}</option>
            </select>
          </div>
        </form>
      </div>
      {this.state.loading && "loading..."}
      <ul className="page-links-content list-group adapt-link-theme">
        {this.state.data && this.state.data
          .filter(this.filterSelectedParts)
          .filter(material => material[Material.TEXTURE].toLowerCase().indexOf(this.state.filterText) > -1).map(material => (
            <li key={material[Material._ID]}
                className="list-group-item">{material[Material._ID]} - {material[Material.TEXTURE]}
              <small onClick={() => {
                this.addPart(material);
              }} className="text-muted pull-right add">add to parts <i className="fa fa-plus"/></small>
            </li>
          ))}
      </ul>
    </div>);
  }
}



