import {CrudPage, getLabel, Item, FormDate, Material} from "./imports";
import {FormSpecs, TableColumns} from "./api/";
import {PAGE_NAME} from "./constants";
import {moment} from "../Material/imports";
import {SearchMaterials, Parts, PartsView} from "./components/";
import commands from "./command";

export const ItemPage = CrudPage({
  pageName: PAGE_NAME,
  FormSpecs,
  TableColumns,
  page: {
    banner: "./home-banner.jpg",
    label: getLabel("LABEL_ITEM"),
    icon: "puzzle-piece"
  },
  formProps: {
    fieldComponent: field => {
      switch (field) {
        case Item.DATEMANUFACTURED:
        case Item.DATETESTED:
          return FormDate;
        case Item.PARTS:
          return Parts;
        default:
          return false;
      }
    },
    viewComponent: field => {
      switch (field) {
        case Item.PARTS:
          return PartsView;
        default:
          return false;
      }
    },
    fieldClass: field => {
      switch (field) {
        case Item.SERIALNO:
          return "col-sm-4 col-sm-offset-right-8";
        case Item.DATEMANUFACTURED:
          return "col-sm-4 col-sm-offset-right-4";
        case Item.PARTS:
          return "col-sm-12";
        default:
          return "col-sm-4";
      }
    },
    viewValueTransformer: field => {
      switch (field) {
        case Item.DATETESTED:
        case Item.DATEMANUFACTURED:
          return (value) => {
            if (value) {
              return moment(value).format("YYYY-MM-DD");
            } else {
              return "N/A";
            }
          };
        default:
          return false;
      }
    },
    modelValueTransformer: (rawValue) => {
      const newValue = {...rawValue};
      newValue.parts = rawValue.parts.map(part => part[Material._ID]);
      newValue[Item.COUNTRY] = process.env.NODE_COUNTRY;
      newValue[Item.COMPANY] = process.env.NODE_COMPANY;
      return newValue;
    }
  },
  overrideHeaders: (page, {defaultControls}) => {
    switch (page) {
      case "list":
        return {
          back: defaultControls.back,
          refresh: defaultControls.refresh,
          assemble: {
            ...defaultControls.create,
            fontIcon: "wrench",
            label: getLabel("LABEL_ASSEMBLE")
          }
        };
      case "create":
        return {
          back: defaultControls.back,
          crate: {
            ...defaultControls.create,
            label: getLabel("LABEL_CREATE")
          }
        };
      case "view":
        return {
          back: defaultControls.back,
          refresh: defaultControls.refresh
        };
      default:
        return {};
    }
  },
  pageLinks: (page) => {
    switch (page) {
      case "create":
        return [
          {
            name: "searchMaterial",
            component: SearchMaterials
          }
        ];
      default:
        return false;
    }
  }
}, commands);



